@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);
.about {
  width: 80%;
  height: 94%;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  margin: auto;
}

.about h1 {
  font-weight: 500;
  font-size: 1em;
  color: #f5f6fa;
}
.about span {
  color: #f5f6fa;
  font-size: 2em;
  font-style: italic;
  font-family: "Dancing Script", cursive;
}

.bg {
  background: url(/movie_box/static/media/harry.5a126604.jpg);
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  background-position: center;
  opacity: 0.3;
}

@media screen and (max-width: 530px) {
  .about h1 {
    font-size: 0.8em;
  }
  .about span {
    font-size: 1.4em;
  }
}

* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

.movie {
  position: relative;
  background-color: #f5f6fa;
  min-width: 400px;
  height: 250px;
}

.movie img {
  position: absolute;
  width: 140px;
  top: -10%;
  left: -5%;
  box-shadow: 2px 2px 10px #2f2f2f;
}

.movie__data {
  width: 100%;
  padding: 30px 0 0 150px;
}

.movie__data .movie__title {
  font-size: 1.5em;
  font-weight: 600;
  margin-bottom: 20px;
  color: #192a56;
}

.movie__data .movie__year {
  color: #a4b0be;
  font-weight: 600;
  margin-bottom: 10px;
}
.movie__data .genres {
  color: #a4b0be;
  display: flex;
  overflow-x: scroll;
  margin-bottom: 20px;
}
.movie__data .genres .genres__genre {
  margin-right: 20px;
}

.movie__data .movie__summary {
  color: #a4b0be;
  white-space: normal;
  overflow: hidden;
  width: 95%;
  height: 3.6em;
  text-overflow: ellipsis;
  line-height: 1.2;
  word-wrap: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
