  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
}

/*font*/

* {
  box-sizing: border-box;
}

html,
body,
.container {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  background: #273c75;
}

#root {
  height: 100%;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f5f6fa;
}

.movies {
  display: grid;
  width: 80%;
  margin: 100px auto;
  grid-template-columns: repeat(auto-fill, minmax(450px, auto));
  grid-column-gap: 50px;
  -webkit-column-gap: 50px;
          column-gap: 50px;
  grid-row-gap: 70px;
  row-gap: 70px;
  grid-area: auto;
  justify-content: center;
}

@media screen and (max-width: 530px) {
  .movies {
    width: 95%;
    grid-template-columns: repeat(auto-fill, minmax(90%, 90%));
  }
  .movie {
    min-width: auto;
    height: 200px;
  }
  .movie__data .movie__title {
    font-size: 1.1em;
  }
  .movie__data .movie__year,
  .movie__data .genres,
  .movie__data .movie__summary {
    font-size: 0.8em;
  }

  .movie img {
    width: 110px;
  }
  .movie__data {
    padding: 14px 0 0 110px;
  }
}

.detail {
  width: 100%;
  margin: 0 auto;
  position: absolute;
  height: 100%;
  top: 0;
  z-index: -2;
}
img.detail__large {
  display: block;
  margin: 100px auto 40px auto;
  width: 65%;
  max-width: 500px;
  box-shadow: 0 0 20px #2f2f2f;
}

img.detail__bg {
  width: inherit;
  height: inherit;
  object-fit: cover;
  object-position: center;
  position: fixed;
  z-index: -2;
  opacity: 0.6;
}

.detail__text {
  width: 80%;
  margin: 0 auto;
  text-align: center;
  color: #f5f6fa;
  margin-bottom: 100px;
}

.detail__title {
  font-size: 2.3em;
  font-weight: 600;
}

.detail__genres {
  list-style: none;
  display: flex;
  justify-content: center;
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
  margin-bottom: 20px;
}
.detail__genre {
  padding: 5px;
  border-radius: 7px;
  background-color: #1e272e;
  font-size: 0.8em;
}

.title__gap {
  margin-bottom: 20px;
  line-height: 30px;
}

.detail__summary {
  font-size: 1.1em;
  line-height: 25px;
}

.detail__summary:first-letter {
  font-size: 1.6em;
}

@media screen and (max-width: 600px) {
  .detail__title {
    font-size: 1.8em;
  }
  .detail__year {
    font-size: 0.8em;
  }
  .detail__summary {
    font-size: 0.9em;
    line-height: 25px;
  }

  .detail__summary:first-letter {
    font-size: 1.4em;
  }
}

@media screen and (max-width: 450px) {
  .detail__title {
    font-size: 1.3em;
  }
  .detail__year {
    font-size: 0.7em;
  }
  .detail__summary {
    font-size: 0.8em;
    line-height: 25px;
  }

  .detail__summary:first-letter {
    font-size: 1.4em;
  }
}

.navigation {
  position: fixed;
  top: 30px;
  left: 10px;
  display: inline-block;
  z-index: 3;
}

.navigator {
  text-decoration: none;
  padding: 10px;
  background-color: #f5f6fa;
  font-weight: 600;
  color: #2f2f2f;
  font-size: 0.8em;
  border-radius: 7px;
  margin-right: 5px;
}
@media screen and (max-width: 530px) {
  .navigator {
    font-size: 0.6em;
  }
}

